        }
    }

    .navigation a {
        position: relative;
        background-color: rgba(0, 0, 0, 0.397);
        border-radius: 50%;
    }

    .icon {
        font-size: 50px;
        border-radius: 50%;
        padding: 10px;
        transition: fill 0.1s ease;
        fill: rgb(209, 217, 252);
    }
}
