.home-container .logo .circle {
    position: absolute;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: rgb(4, 4, 44);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s ease;
    opacity: 0;

}

.home-container .logo .hover-show.active .circle {
    opacity: 1;
    -webkit-box-shadow: inset 0 0 32px 5px rgb(4, 4, 44);
    -moz-box-shadow: inset 0 0 32px 5px rgb(4, 4, 44);
    box-shadow: inset 0 0 32px 5px rgb(4, 4, 44);

}

.home-container .logo .hover-show.active .circle:nth-child(1) {
    background: url(../props/js.jpg);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translateY(-210%);

}

.home-container .logo .hover-show.active .circle:nth-child(2) {
    position: absolute;
    transform: translateX(150%) translateY(-150%);
    background: url(../props/react.png);
    background-size: cover;
    background-position: center;

}

.home-container .logo .hover-show.active .circle:nth-child(3) {
    position: absolute;
    transform: translateX(210%);
    background: url(../props/redux.png);
    background-size: cover;
    background-position: center;
}

.home-container .logo .hover-show.active .circle:nth-child(4) {
    position: absolute;
    transform: translateX(150%) translateY(150%);
    background: url(../props/materialui.png);
    background-size: cover;
    background-position: center;
}

.home-container .logo .hover-show.active .circle:nth-child(5) {
    position: absolute;
    transform: translateY(210%);
    background: url(../props/bootstrap.png);
    background-size: cover;
    background-position: center;
}

.home-container .logo .hover-show.active .circle:nth-child(6) {
    position: absolute;
    transform: translateX(-150%) translateY(150%);
    background: url(../props/mongo.db.png);
    background-size: cover;
    background-position: center;
}

.home-container .logo .hover-show.active .circle:nth-child(7) {
    position: absolute;
    transform: translateX(-210%);
    background: url(../props/node.js.png);
    background-size: cover;
    background-position: center;
}

.home-container .logo .hover-show.active .circle:nth-child(8) {
    position: absolute;
    transform: translateX(-150%) translateY(-150%);
    background: url(../props/htmlcss.png);
    background-size: cover;
    background-position: center;
}

.home-container img {
    position: absolute;
    height: 300px;
    width: 300px;
    border-radius: 50%;
    border: 3px solid rgb(108, 161, 197);
    transform: translate(-200px);
    opacity: 0;
    animation: fade-in 1s ease 1 0.5s forwards;
}

@keyframes fade-in {
    to {
        transform: translate(0);
        opacity: 1;
    }
}

.home-container h2 {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 60%;
    margin-top: 50px;
}

.home-container h2 span {
    font-size: 25px;
    opacity: 0.8;
    padding-bottom: 5px;
    border-bottom: 2px solid var(--color-pri);
    font-family: "Open Sans Condensed", sans-serif;
}

.home-container h2 p {
    font-size: 20px;
    opacity: 1;
    font-weight: 100;
    margin-top: 1px;
    font-family: "Open Sans", sans-serif;
}

.scroll-down {
    transform: rotate(90deg);
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.scroll-down hr {
    background-color: blue;
    width: 20px;
}

.scroll-down h5 {
    font-size: 18px;
    text-transform: capitalize;
    font-weight: 100;
}

.scroll-down .scroll {
    transform: rotate(-90deg);
    font-size: 20px;
}

